.contact {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 20px 0;
  background-color: #06325e;
  text-align: center;
  opacity: 0.9;
  border-top-left-radius: 15%;
  font-family: Arial, Helvetica, sans-serif;
}

.contact-header {
  margin-bottom: 30px;
  margin-inline: 10%;
}

.contact h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
  font-weight: 800;
  background-image: linear-gradient(to right, var(--tw-gradient-stops));

  --tw-gradient-from: #a0c0e6 var(--tw-gradient-from-position);
  --tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to);
  --tw-gradient-to: #dfb7b7 var(--tw-gradient-to-position);

  -webkit-background-clip: text;
  color: transparent;
}

.contact p {
  font-size: 1.5rem;
  margin-bottom: 20px;
  background-image: linear-gradient(to right, var(--tw-gradient-stops));

  --tw-gradient-from: #bbe6db var(--tw-gradient-from-position);
  --tw-gradient-stops: var(--tw-gradient-from), var(--tw-gradient-to);
  --tw-gradient-to: #e8ecde var(--tw-gradient-to-position);

  -webkit-background-clip: text;
  color: transparent;
}

.contact h3{
  font-size: 1.5rem;
  margin-bottom: 20px;
  font-weight: 800;
  color: aqua;
}

.contact .contact-form-container {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  opacity: 0.8;
}

.contact .contact-form-cont {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.contact .contact-form {
  background-color: #fff;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  width: 100%;
  margin-right: 20px;
}

.contact .form-element {
  margin-bottom: 30px;
  width: 90%;
}

.contact label {
  display: block;
  font-weight: bold;
  font-size: 1.2rem;
}

.contact input,
.contact textarea {
  width: 100%;
  padding: 15px;
  margin-top: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
}

.contact textarea {
  height: 150px;
}

.contact .submit-button {
  background-color: #007bff;
  color: #fff;
  padding: 15px 30px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.2rem;
}

.contact .submit-button:hover {
  background-color: #308db8;
  cursor: pointer;
}

.contact .success-message {
  margin-top: 30px;
  background-color: #d4edda;
  border: 1px solid #c3e6cb;
  border-radius: 10px;
  padding: 20px;
  font-size: 1.2rem;
}

@media (max-width: 768px) {
  .contact {
    display: flex;
    flex-direction: column;
    padding: 20px;
    background-color: #06325e;
    text-align: center;
    opacity: 0.9;
    border-top-left-radius: 15%;
    font-family: Arial, Helvetica, sans-serif;
  }

  .contact-header {
    margin: 0;
  }

  .contact h2 {
    font-size: 2rem;
    margin-bottom: 15px;
  }

  .contact p {
    display: none;
  }

  .contact .forMobile {
    display: block;
    font-size: 1.2rem;
  }

  .contact .contact-form-container {
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0.8;
  }

  .contact .contact-form {
    padding: 20px;
    border-radius: 10px;
    width: 100%;
    margin-top: 20px;
  }

  .contact .form-element {
    margin-bottom: 20px;
  }

  .contact label {
    font-size: 1rem;
  }

  .contact input,
  .contact textarea {
    width: 100%;
    padding: 10px;
    margin-top: 5px;
  }

  .contact textarea {
    height: 100px;
  }

  .contact .submit-button {
    padding: 10px 20px;
    font-size: 1rem;
  }

  .contact .submit-button:hover {
    background-color: #308db8;
    cursor: pointer;
  }

  .contact .success-message {
    margin-top: 20px;
    padding: 15px;
    font-size: 1rem;
  }
}
